<template>
  <div class="container">
    <h2>东北师范大学化学学院答题系统</h2>
      <form class="form-signin">
          <h4 class="form-signin-heading">请登录</h4>
          <label for="inputStuno" class="sr-only">学号</label>
          <input type="text" id="inputStuno" v-model="username" class="form-control" placeholder="学号" required="" autofocus="">
          <label for="inputPassword" class="sr-only">密码</label>
          <input type="password" id="inputPassword" v-model="password" class="form-control" placeholder="密码" required="">
          <button class="btn btn-lg btn-primary btn-block" v-on:click="submit" type="button">登录</button>
        </form>
    </div>
</template>

<script>
export default {
  name: 'Loginin',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    submit: function () {
      this.axios.post('/loginReginster/login',this.$qs.stringify({username: this.username, password: this.password}))
      .then(res => {
        console.log(res.data);
        console.log(res.data.level);
        if(res.data.level == 0){      
          this.$router.push('/selectCourse');
        }
      }, res => {
          console.log(res.data.status);
      })

      // this.axios({
      //   method: 'post',
      //   url: 'http://47.93.197.5/loginReginster/login',
      //   data: {
      //     username: this.username,
      //     password: this.password
      //   },
      //   mode: 'no-cors',  
      //   headers: {  
      //     'Access-Control-Allow-Origin': '*',  
      //     'Content-Type': 'application/json',  
      //   },  
      //   withCredentials: true,  
      //   credentials: 'same-origin',  
      // }).then(res => {  
      //   console.log(res.level);
      //   console.log(res.status);
      // }, res => {
      //     console.log(res.status);
      // });
    }
  }
}
</script>

