<template>
    <div class="container">
        <form class="form-choice">
            <!-- <div id="searchQues">
                <span class="help-block">请选择章：</span>
                <select id="searchCha" class="form-control">
                    <option v-for="chapter in Chapter" value="1">{{chapter.chapterName}}</option>
                </select> -->

                <!-- <span class="help-block">请选择节：</span>
                <select id="searchSec" class="form-control">
                    <option value="2" v-for="section in Section">{{section.sessionName}}</option>
                </select> -->

                <!-- <button class="btn btn-primary btn-lg btn-block" @click="searchQues" style="margin: 20px 0px;">查询</button> -->
            <!-- </div> -->

            <div class="choiceBlock" v-for="(item,index) in Exercise">
                <template v-if="item.type == 0">
                    <p class="choiceQuestion"><em>{{item.choiceQuesId}}.</em>{{item.quesDescription}}</p>
                    <label class="option"><input type="radio" class="radio-hidden" v-bind:name="item.choiceQuesId" value="A"><span class="radio-show"></span>A.{{item.optionA}}</label>
                    <label class="option"><input type="radio" class="radio-hidden" v-bind:name="item.choiceQuesId"  value="B"><span class="radio-show"></span>B.{{item.optionB}}</label>
                    <label class="option"><input type="radio" class="radio-hidden" v-bind:name="item.choiceQuesId"  value="C"><span class="radio-show"></span>C.{{item.optionC}}</label>
                    <label class="option"><input type="radio" class="radio-hidden" v-bind:name="item.choiceQuesId"  value="D"><span class="radio-show"></span>D.{{item.optionD}}</label>
                </template>
                <template v-else-if="item.type == 1">
                    <p class="choiceQuestion"><em>{{item.judgmentQuesId}}.</em> {{item.judgQuesDescription}}</p>
                    <label class="option"><input type="radio" class="radio-hidden" v-bind:name="item.choiceQuesId"  value="对"><span class="radio-show"></span>对</label>
                    <label class="option"><input type="radio" class="radio-hidden" v-bind:name="item.choiceQuesId"  value="错"><span class="radio-show"></span>错</label>
                </template>
            </div>
        </form>
    </div>
</template>

<script>
export default {
    name: 'choice',
    data() {
        return {
            Exercise: [],
            Chapter: [],
            Section: []
        }
    },
    mounted: function(){
        this.getOpenQues();
        // this.getChapter();
        // this.getSession();
    },
    methods: {
        getOpenQues: function(){
            this.axios.get('/answer/student/getOpenQues')
            .then(res => {
                this.Exercise = res.data;
            }, res => {
                console.log(res);
            })
        }
        // getChapter: function(){
        //     this.axios.get('/ques/getChapter')
        //     .then(res => {
        //         console.log(res.data);
        //         this.Chapter = res.data;
        //     }, res => {
        //         console.log(res)
        //     })
        // }
        // getSession: function(){
        //     this.axios.get('/ques/getSession')
        //     .then(res => {
        //         console.log(res.data);
        //         this.Section = res.data;
        //     }, res => {
        //         console.log(res);
        //     })
        // }
    } 
}
</script>

<style scoped>
    .btn {
        margin-top: 20px;
        margin-bottom: 20px;
    }
</style>


